@media screen and (max-width: 1080px) {
    .video-carousel-section {
        grid-column: span 12;
        height: 100%;
        overflow: visible;
    }

}

@media screen and (max-width: 640px) {
    .image-carousel {
        width: 90%;
    }

    .image-carousel-container {
        margin: 0px auto;
    }
}